.App {
  text-align: center;
  background-color: #ecf0f1;
  height: 100vw; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-container {
  background-color: #ecf0f1; }
  @media (max-device-width: 559px) {
    .App-container {
      margin: 0% 0 2% 0;
      padding: 0 .5em 20px .5em; } }
  @media (min-width: 560px) and (max-width: 1023px) {
    .App-container {
      margin: 3% 10% 3% 10%;
      padding: 30px; } }
  @media (min-width: 1024px) {
    .App-container {
      margin: 3% 10% 3% 10%;
      padding: 30px; } }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.input-form {
  border: 2px solid #95a5a6;
  margin-bottom: 20px;
  overflow: hidden;
  height: 44px;
  vertical-align: middle;
  padding-left: 14px;
  font-size: 1em;
  color: #2c3e50;
  font-family: Georgia;
  width: calc(100% - 22px); }
  .input-form:focus {
    border-color: #2980b9;
    outline: none; }

.input-container {
  width: 100%;
  text-align: center; }
  .input-container .input-form {
    display: inline-block; }

body {
  margin: 0;
  padding: 0;
  font-family: Georgia;
  background-color: #ecf0f1; }

.panel {
  background-color: white;
  color: #2980b9;
  border: 1px solid transparent;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
  text-align: left; }
  @media (max-device-width: 559px) {
    .panel {
      margin-bottom: 2px; } }
  @media (min-width: 560px) and (max-width: 1023px) {
    .panel {
      margin-bottom: 5px; } }
  @media (min-width: 1024px) {
    .panel {
      margin-bottom: 5px; } }
  .panel:last-child {
    margin-bottom: 0; }

.panel-default {
  border-color: #ddd; }
  .panel-default:hover {
    border-left: 4px solid #2980b9;
    margin-left: -3px; }

.panel-default > .panel-header {
  font-size: 1.25em;
  border-color: #ddd; }
  .panel-default > .panel-header img {
    float: left;
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin-right: 10px;
    max-width: 100%; }
  .panel-default > .panel-header a {
    float: none;
    display: inline;
    color: #2980b9;
    -webkit-text-decoration-line: none;
            text-decoration-line: none; }
    .panel-default > .panel-header a:hover {
      -webkit-text-decoration-line: underline;
              text-decoration-line: underline; }

.panel-header {
  padding: 10px 15px 0px 15px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px; }

.panel-body {
  padding: 5px 15px 15px 15px;
  color: #2c3e50; }

.App-header {
  background-color: #ecf0f1;
  padding: 20px 20px 0 20px;
  font-weight: bold;
  font-size: 30px;
  color: #2980b9;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: #34495e; }



